.card-holder {
    width: 35vmin;
    height: 60vmin;
    margin-left: 3vmin;
    margin-right: 3vmin;
    display: inline-block;
    position: relative;
}

.card {
    width: 35vmin;
    height: 60vmin;
    background-color: #bab9b6;
    background-size: cover;
    background-position: center;
    border-radius: 3px;
    position: absolute;
    transform-style: preserve-3d;
    transition: 2s ease-out;
    transition-timing-function: ease-out;
}

.card.back {
    margin-left: 0;
    margin-top: 0;
    width: 35vmin;
    height: 60vmin;
    background-size: cover;
    background-position: center;
    border-radius: 3px;
    transform: rotateX(360deg)
}

.background-card {
    width: 35vmin;
    height: 60vmin;
    display: block;
    position: absolute;
    transform: rotate(-4deg);
    margin-top: 20px;
    margin-left: -16px;

    background: #080808;
    background: linear-gradient(to right, #545454 50%, #080808 50%) left;
    background-size: 200%;
    transition: .4s ease-out;
}

.background-card.hover{
    background-position: right;
}

.top-overlay {
    width: 100%;
    height: 3vmin;
    background-color: rgba(30, 30, 30, 0.60);
    border-radius: 3px 3px;
    position: absolute;
    font-weight: bold;
    font-family: 'Maven Pro', sans-serif;
    font-size: 2vmin;
}

.bottom-overlay {
    width: 100%;
    height: 17vmin;
    background-color: rgba(30, 30, 30, 0.60);
    border-radius: 0 0 3px 3px;
    position: absolute;
    font-family: 'Maven Pro', sans-serif;
    font-size: 2vmin;
    bottom: 0;
}

.player-name {
    font-weight: bold;
    font-family: 'Maven Pro', sans-serif;
    font-size: 2vmin;
}

.card-ul{
    list-style-type: none;
    margin-top: 1vmin;
    padding-left: 0px;
}

.card-li{
    width: 100%;
    text-align: left;
    padding-bottom: .5vmin;
    border-bottom: 1px solid rgb(206, 206, 206);
    display: flex;
    justify-content: space-between;
}

.card-li-p-front {
    font-size: 2vmin;
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 1vmin;
    display: inline-block;
}

.card-li-p-parse {
    font-size: 2vmin;
    margin-top: 0;
    margin-bottom: 0;
    display: inline-block;
    padding-right: 1vmin;
}
